.subscription__form-input input {
  width: 100%;
  padding: 14px 0 5px;
  color: #ffffff;
  border: none;
  border-bottom: 1px solid #74819c;
  background: none;
}
.subscription__form-input input::placeholder {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: #ffffff;
}
@media (hover: hover) {
  .subscription__form-input input:hover:not(.focus-visible) {
    border-bottom: 1px solid #ffffff;
  }
}
.subscription__form-input input:active, .subscription__form-input input:focus {
  padding-left: 15px;
  border-bottom: none;
  outline: 1px solid #ffffff;
}
.subscription__form-input input:active::placeholder, .subscription__form-input input:focus::placeholder {
  visibility: hidden;
}
.subscription__form-input input:disabled {
  opacity: 0.3;
}
.subscription__form-input.is-invalid {
  position: relative;
}
.subscription__form-input.is-invalid input {
  border-bottom: 1px solid #ff121f;
}
.subscription__form-input.is-invalid::after {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 80px;
  min-height: 20px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  color: #ff121f;
  content: " ";
}

